html {
  box-sizing: border-box;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden; //     position: sticky -   
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  height: 100%;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: $font;
  font-size: 16px;
  line-height: 1.6;
  color: $text-color;
}

img {
  height: auto;
  max-width: 100%;
}

p {
  margin: 0;
  padding: 0;
}

[data-object-fit-cover] {
  display: block;
  object-fit: cover;
  object-position: center;
  font-family: "object-fit: cover; object-position: center;";
  width: 100%;
  height: 100%;
}

[data-object-fit-cover-right] {
  display: block;
  object-fit: cover;
  object-position: right center;
  font-family: "object-fit: cover; object-position: right center;";
  width: 100%;
  height: 100%;
}

[data-object-fit-contain] {
  display: block;
  object-fit: contain;
  object-position: center;
  font-family: "object-fit: contain; object-position: center;";
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  color: $text-color;
}

.site-container {
  flex-grow: 1;

  &_second {
    padding-top: 151px;
  }
}

.xbtn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #FFFFFF;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

input[type="submit"] {
  &[disabled] {
    cursor: no-drop;
    background: #5E5E5E;
  }
}

.accent-color {
  color: $accent;
}

.bg-wallpaper {
  background: #FFFFFF url("../img/bg-wallpaper.jpg") repeat center center;
}

.bg-grey {
  background-color: #F7F7F7;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 $container-offset;
  max-width: $container-width;
}

.clearfix::after {
  display: table;
  clear: both;
  content: "";
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.icon {
  max-width: 100%;
  max-height: 100%;
  fill: $icon-color;
  transition: 0.5s;
}

.to-top {
  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: 100;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  background: rgba($accent, 0.8);
  border-radius: 6px;
  box-shadow: 0px 0px 10px 0px rgba($accent,0.3);
  cursor: pointer;
  transition: 0.5s;
  opacity: 0;
  height: 0;
  overflow: hidden;
  z-index: 250;

  .icon {
    width: 12px;
    height: 12px;
    fill: #FFFFFF;
    transform: rotate(-90deg);

    &:hover {
      opacity: 1;
      box-shadow: 0 0 10px 0 rgba($accent, 0.7);
    }

    &.fixed {
      opacity: 1;
      height: 37px;
    }
  }
}

/*   yandex map */
[class$="-controls-pane"],
[class$="-copyrights-pane"] {
  display: none;
}

/*   swiper */
.swiper-wrapper {
  & * {
    box-sizing: border-box;
  }
}

.swiper-slide {
  height: auto;
}

.swiper-button-prev,
.swiper-button-next {
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  outline: none;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  width: 55px;
  height: 55px;
  margin: 0;
  background-color: #FFFFFF;
  border-radius: 50%;
  transition: 0.3s;

  &::after {
    content: "";
    width: 8px;
    height: 14px;
    background-color: transparent;
    background-image: url("../img/arrow.svg");
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    border: none;
  }
}

.swiper-button-prev {
  left: -27px;
}

.swiper-button-next {
  right: -27px;

  &::after {
    transform: rotate(180deg);
  }
}

.swiper-pagination-lock {
  display: none;
}

.swiper-button-disabled {
  display: none;
}

.inscr {
  display: flex;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.6;

  &::before {
    content: "";
    display: block;
    width: 17px;
    height: 24px;
    margin-right: 21px;
    margin-top: 4px;
    background-image: url("../img/flag.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }
}

.section-title {
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 40px;
  line-height: 1.4;
  text-align: center;

  &_second {
    margin-bottom: 29px;
    font-size: 36px;
    text-align: left;
  }
}

.section-desc {
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
}

.mr-0 {
  margin-right: 0 !important;
}

.logo {
  display: flex;
  align-items: center;

  &__img {
    display: block;
    text-decoration: none;
    width: 207px;
    height: 64px;
    margin-right: 26px;
  }

  &__text {
    font-size: 13px;
    line-height: 1.153;
  }
}

.burger {
  flex-shrink: 0;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background-color: #EF7147;
  border-radius: 6px;

  &__wrap {
    position: relative;
    display: block;
    margin-top: 4px;
    transition: 0.3s;
    z-index: 100;
  }

  .ham {
    width: 35px;
    cursor: pointer;
    transition: transform 400ms;
    user-select: none;
  }

  .hamRotate.active {
    transform: rotate(45deg);
  }

  .hamRotate180.active {
    transform: rotate(180deg);
  }

  .line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: #FFFFFF;
    stroke-width: 5.5;
    stroke-linecap: round;
  }

  .ham1 .top {
    stroke-dasharray: 40 139;
  }

  .ham1 .bottom {
    stroke-dasharray: 40 180;
  }

  .ham1.active .top {
    stroke-dashoffset: -98px;
  }

  .ham1.active .bottom {
    stroke-dashoffset: -138px;
  }
}

.text-underline {
  text-decoration: underline;
}

.tabsNav {
  display: flex;
  justify-content: center;

  & > li {
    text-align: center;

    &:not(:last-child) {
      margin-right: 31px;
    }

    a {
      min-width: 320px;
      padding: 12px 15px;
      font-size: 20px;
      line-height: 1.6;
      border-radius: 40px;
    }

    &.current {
      .tabsNav__btn {
        &::before {
          box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.35);
          background-image: none;
          background-color: #FE864A;
        }

        &::after {
          opacity: 0;
        }
      }

      a {
        &:hover {
          background-color: transparent;
        }
      }
    }
  }
}

.to-top {
  position: fixed;
  right: 20px;
  bottom: 80px;
  z-index: 100;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 0;
  height: 37px;
  background-color: $accent;
  background-image: url('../img/arrow-white.svg');
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: center center;
  border-radius: 6px;
  box-shadow: 0 0 10px 0px rgba(#000000, 0.1);
  transform: rotate(90deg);
  cursor: pointer;
  transition: 0.5s;
  opacity: 0;
  overflow: hidden;
  z-index: 250;
}

.to-top:hover {
  opacity: 1;
}

.to-top.fixed {
  opacity: 0.8;
  width: 37px;
}

.menuOpen {
  justify-content: space-between;
  width: 100%;
  padding: 20px 24px 20px 20px;
  background-color: #F7F7F7;
  border-radius: 10px;
  text-align: left;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.4;
  text-transform: uppercase;

  &__link {
    cursor: pointer;

    &_active {
      text-decoration-line: underline;
      color: #808080;
    }
  }
}

.sortItem {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.6;
  color: #686868;

  &__text {
    font-weight: 500;
  }
}

.tooltip {
  position: relative;
  width: 16px;
  height: 19px;
  border-radius: 100%;
  background-color: transparent;
  background-image: url("../img/i.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  z-index: 10;

  &:hover {
    .tooltip__hidden {
      opacity: 1;
      max-width: none;
      max-height: none;
      padding: 10px 15px;
      z-index: 100;
    }
  }

  &__hidden {
    position: absolute;
    top: calc(100% + 5px);
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 250px;
    max-width: 0;
    max-height: 0;
    font-weight: 400;
    font-size: 12px;
    font-style: normal;
    text-transform: none;
    background-color: #FFFFFF;
    box-shadow: 0 0 3px rgba(#000000, 0.2);
    opacity: 0;
    overflow: hidden;
    transition: opacity 0.3s;
    z-index: -1000;
  }
}

/*   nice-select */
.nice-select {
  width: 100%;
  border: none;
  background-color: transparent;

  &::after {
    width: 7px;
    height: 7px;
    border-color: #000000;
  }

  .list {
    width: 100%;

    & > li {
      display: flex;
      align-items: center;
      white-space: normal;
      line-height: 1.4;
    }
  }

  .option {
    padding-left: 11px;
    padding-right: 11px;
    font-size: 13px;
  }

  .current {
    display: block;
    overflow: hidden;
    transition: color 0.3s;

    &:hover {
      color: $accent;
    }
  }
}

@include max(1650) {
  .inscr {
    font-size: 16px;

    &::before {
      width: 14px;
      height: 20px;
      margin-right: 11px;
      margin-top: 1px;
    }
  }

  .container {
    max-width: $content-width-small;
  }

  .section-title {
    font-size: 36px;

    &_second {
      margin-bottom: 19px;
      font-size: 28px;
    }
  }

  .section-desc {
    font-size: 18px;

    br {
      display: none;
    }
  }
}

@include max(1440) {
  .tabsNav {
    & > li {
      a {
        min-width: 260px;
        padding: 12px 15px;
        font-size: 16px;
      }
    }
  }
}

@include max(1200) {
  .logo {
    &__img {
      width: 167px;
      height: 54px;
      margin-right: 16px;
    }

    &__text {
      font-size: 11px;
    }
  }

  .tabsNav {
    & > li {
      &:not(:last-child) {
        margin-right: 15px;
      }

      a {
        min-width: 180px;
        font-size: 14px;
      }
    }
  }

  .site-container {
    &_second {
      padding-top: 136px;
    }
  }
}

@include max(992) {
  .section-title {
    margin-bottom: 8px;
    font-size: 30px;
  }

  .section-desc {
    margin-bottom: 33px;
    font-size: 16px;
  }

  .logo {
    margin-right: 10px;

    &__img {
      width: 108px;
      height: 34px;
      margin-right: 0;
    }

    &__text {
      display: none;
    }
  }

  .site-container {
    &_second {
      padding-top: 56px;

      &::before {
        content: "";
        position: absolute;
        top: 56px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: rgba(#000000, 0.1);
      }
    }
  }
}

@include max(768) {
  .section-title {
    margin-bottom: 16px;
    font-size: 24px;
  }

  .section-desc {
    font-size: 14px;
    line-height: 1.4;
  }

  .tabsNav {
    & > li {
      &:not(:last-child) {
        margin-right: 6px;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 100px;
        height: 44px;
        padding: 9px 5px;
        font-size: 13px;
        line-height: 1;
      }
    }

    .tabsNav__btn {
      &::after {
        content: none;
      }
    }
  }
}

@include max(470) {
  .inscr {
    font-size: 14px;

    &::before {
      width: 19px;
      height: 18px;
    }
  }

  .section-title {
    &_second {
      margin-bottom: 8px;
      font-size: 20px;
    }
  }
}

@include max(374.98) {
  .logo {
    margin-right: 10px;

    &__img {
      width: 98px;
      height: 26px;
    }
  }

  .tabsNav {
    & > li {
      a {
        font-size: 11px;
      }
    }
  }
}
