.schemes {
  padding-top: 26px;
  padding-bottom: 73px;

  &_card {
    padding-bottom: 38px;

    .schemes__tabsNav {
      margin-bottom: 46px;
    }
  }

  &__price {
    display: block;
    margin-top: 18px;
    font-weight: 500;
    font-size: 20px;

    b {
      font-weight: 500;
      font-size: 32px;
    }
  }

  &__tab {
    &:not(:nth-child(2)) {
      display: none;
    }
  }

  &__tabsNav {
    margin-bottom: 25px;
  }

  &__partsNav {
    display: flex;

    & > li {
      & > a {
        position: relative;
        display: block;
        min-width: 230px;
        padding: 18px 40px;
        background: linear-gradient(360deg, #DFE6ED -24.24%, #FEFFFF 112.12%), #EFF0F0;
        border-radius: 6px 6px 0 0;
        font-weight: 500;
        font-size: 18px;
        text-align: center;
        overflow: hidden;
        z-index: 1;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          z-index: -2;
          opacity: 0;
          background: linear-gradient(180deg, #DFE6ED -64.81%, rgba(223, 230, 237, 0) 100%), #FFFFFF;
          transition: opacity 0.3s;
        }

        &::after {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          z-index: -1;
          opacity: 0;
          background: linear-gradient(180deg, #DFE6ED -64.81%, rgba(223, 230, 237, 0.8) 100%), #FFFFFF;
          transition: opacity 0.3s;
        }
      }

      &.current {
        & > a {
          &::before {
            opacity: 1;
          }
        }
      }

      &:not(.current) {
        & > a {
          &:hover {
            &::after {
              opacity: 1;
            }
          }
        }
      }

      &:not(:last-child) {
        & > a {
          border-right: 1px solid #D6E1EB;
        }
      }
    }
  }

  &__wrap {
    padding: 35px 30px 35px 67px;
    background: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 0 0 25px 25px;
  }

  &__btns {
    margin-top: -31px;
    text-align: center;

    .xbtn {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  &__partsTab {
    &:not(:first-child) {
      display: none;
    }
  }

  &__partsCols {
    display: flex;
    align-items: flex-start;
  }

  &__partsMenu {
    margin-right: 47px;
    counter-reset: num;

    & > li {
      display: flex;

      &:not(:last-child) {
        margin-bottom: 15px;
      }

      &::before {
        flex-shrink: 0;
        content: counter(num);
        counter-increment: num;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        margin-right: 21px;
        border: 1px solid $accent;
        border-radius: 50%;
        font-size: 18px;
        font-weight: 500;
      }
    }
  }

  &__partsBtn {
    font-size: 14px;
    line-height: 1.65;
    text-align: left;
    transition: color 0.3s;

    &:hover {
      color: $accent;
    }

    &_current {
      text-decoration: underline;
    }
  }

  &__plans {
    position: relative;
    margin-top: -20px;
  }

  &__plansList {
    counter-reset: plansNum;
  }

  &__plansItem {
    position: absolute;
    z-index: 2;
    transition: transform 0.1s;

    &_visible {
      transform: scale(1.15);
      z-index: 3;
      transition: transform 0.2s;
    }

    &::before {
      content: counter(plansNum);
      counter-increment: plansNum;
      position: absolute;
      top: -15px;
      left: -13px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background: linear-gradient(0deg, #FF634D 0%, #FE864A 61.54%, #FE864A 100%), #D9D9D9;
      border: 2px solid #FFFFFF;
      box-shadow: 0px 5px 10px rgba(203, 65, 62, 0.3);
      font-weight: 500;
      font-size: 18px;
      color: #FFFFFF;
      z-index: 2;
    }
  }

  &__plansInner {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 285px;
    padding: 20px 10px 20px 20px;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 8px;
    font-size: 14px;
    opacity: 0;
    overflow: hidden;
    transform: scale(0);
    transition: opacity 0.3s, transform 0s 0.3s;

    &_visible {
      opacity: 1;
      transform: scale(1);
      transition: opacity 0.3s;
    }

    &_right {
      left: auto;
      right: 0;
    }
  }

  @include max(1650) {
    &__partsMenu {
      & > li {
        &:not(:last-child) {
          margin-bottom: 11px;
        }
      }
    }

    &__plans {
      max-width: 460px;
    }
  }

  @include max(1440) {
    &__price {
      b {
        font-size: 26px;
      }
    }
  }

  @include max(1200) {
    &__wrap {
      padding: 35px 30px;
    }

    &__partsMenu {
      margin-right: 17px;
    }

    &__partsNav {
      & > li {
        & > a {
          min-width: 190px;
          padding: 13px 20px;
          font-size: 16px;
        }
      }
    }

    &__price {
      b {
        font-size: 20px;
      }
    }

  }

  @include max(992) {
    &__wrap {
      padding: 25px 15px 35px;
    }

    &__partsBtn {
      font-size: 13px;
    }

    &__partsMenu {
      & > li {
        &::before {
          width: 24px;
          height: 24px;
          margin-right: 11px;
          font-size: 14px;
        }
      }
    }
  }

  @include max(768) {
    padding-bottom: 43px;

    &__wrap {
      padding: 25px 15px 35px;
    }

    &__partsMenu {
      display: none;
    }

    &__plans {
      max-width: 400px;
      margin: 0 auto;
    }

    &__plansItem {
      &::before {
        top: -10px;
        left: -9px;
        width: 20px;
        height: 20px;
        font-size: 13px;
      }
    }

    &__plansInner {
      width: 197px;
      padding: 10px;
      font-size: 12px;
      line-height: 1.2;
    }

    &__btns {
      .xbtn {
        padding: 15px 46px;
        font-size: 14px;
        line-height: 1.2;
      }
    }

    &__price {
      margin-top: 6px;
      font-size: 14px;

      b {
        display: block;
        font-size: 14px;
      }
    }

    &__partsNav {
      & > li {
        & > a {
          min-width: 89px;
          padding: 8px 14px;
          font-size: 15px;
          line-height: 1.2;
        }
      }
    }
  }

  @include max(576) {
    &__partsNav {
      & > li {
        & > a {
          display: flex;
          align-items: center;
          height: 100%;
          font-size: 13px;
        }
      }
    }

    &__plansInner {
      width: 167px;
    }
  }

  @include max(374.98) {
    &__plansInner {
      width: 145px;
      padding: 7px;
      font-size: 11px;
    }
  }
}
