.projects {
  padding-top: 40px;
  padding-bottom: 91px;

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 47px 36px;
  }

  &__item {
    background: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;
  }

  &__link {
    display: block;
    border-radius: 25px;
    overflow: hidden;
  }

  &__img {
    width: 100%;
  }

  &__content {
    padding: 0 32px;
  }

  &__title {
    position: relative;
    width: 87%;
    max-width: 528px;
    margin: -50px auto 24px;
    padding: 19px 10px 10px;
    background-color: #FFFFFF;
    border-radius: 15px;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.4;
    text-transform: uppercase;
    text-align: center;
    z-index: 1;
  }

  &__info {
    display: flex;
    padding-top: 37px;
    padding-bottom: 32px;
    border-top: 1px solid #F78D20;
  }

  &__infoItem {
    margin-right: 71px;
  }

  &__infoTitle {
    display: block;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
    color: #686868;
  }

  &__infoText {
    display: block;
    font-weight: 500;
    font-size: 32px;

    sup {
      font-size: 16px;
    }

    span {
      font-size: 20px;
    }
  }

  &__infoBtns {
    margin-left: auto;
    padding-top: 5px;
  }

  @include max(1650) {
    &__infoItem {
      margin-right: 31px;
    }

    &__infoText {
      font-size: 25px;
    }

    &__infoTitle {
      font-size: 12px;
    }

    &__title {
      max-width: 408px;
      margin: -35px auto 14px;
      padding: 10px;
      font-size: 18px;
    }

    &__info {
      padding-top: 15px;
      padding-bottom: 25px;
    }

    &__content {
      padding: 0 14px;
    }

    &__list {
      grid-gap: 20px 15px;
    }

    &__img {
      height: 300px;
      object-fit: cover;
    }

    &__infoText {
      font-size: 20px;
    }

    &__title {
      margin-top: -58px;
      font-size: 16px;
      line-height: 1.4;
    }
  }

  @include max(1300) {
    &__info {
      flex-wrap: wrap;
    }

    &__infoBtns {
      margin-left: auto;
      margin-right: auto;
    }
  }

  @include max(768) {
    padding-top: 15px;
    padding-bottom: 37px;
  }

  @include max(576) {
    &__list {
      display: block;
    }

    &__item {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &__infoBtns {
      margin-top: 14px;
      padding-top: 0;
    }
  }
}
