.features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 36px;

  &__item {
    position: relative;
    padding: 36px 40px 35px 34px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    transition: transform 0.4s;

    &:hover {
      transform: translateY(-7px);
    }

    &::before {
      content: "";
      position: absolute;
      top: -11px;
      left: 30px;
      width: 30px;
      height: 30px;
      background-image: url("../img/tick.svg"),
                        $grad;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 10px 7px,
                       cover;
      box-shadow: 0 5px 10px rgba(203, 65, 62, 0.3);
      border-radius: 50%;
    }
  }

  &__text {
    font-size: 14px;

    b {
      font-weight: 500;
    }
  }

  @include max(1650) {
    &__item {
      padding: 25px;
    }

    &__text {
      font-size: 13px;
    }
  }
}
