.faq {
  position: relative;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 97px;
    padding: 16px 16px 16px 41px;
  }

  &__item {
    position: relative;
    background: #F7F7F7;
    border-radius: 15px;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 28px;
    }
  }

  &__hidden {
    display: none;
    padding: 18px 75px 40px 41px;
    font-size: 14px;
    line-height: 1.65;
  }

  &__title {
    margin: 0 15px 0 0;
    font-weight: 500;
    font-size: 20px;
  }

  &__item p:last-of-type {
    margin-bottom: 0;
  }

  &__item a {
    color: #000000;
    text-decoration: underline;
    transition: 0.3s;
  }

  &__item a:hover {
    color: $accent;
  }

  &__open {
    flex-shrink: 0;
    position: relative;
    background: none;
    border: none;
    outline: none;
    display: block;
    width: 44px;
    height: 44px;
    background: #FFFFFF url("../img/cross.svg") no-repeat center center;
    background-size: 24px 24px;
    transform: rotate(45deg);
    transform-origin: center center;
    transition: 0.4s ease-in-out;
    border-radius: 50%;

    &.active {
      transform: rotate(-90deg);
    }
  }

  @include max(1650) {
    &__top {
      min-height: 82px;
      padding: 16px 16px 16px 41px;
    }

    &__title {
      font-size: 16px;
      line-height: 1.4;
    }
  }

  @include max(768) {
    &__item {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    &__top {
      min-height: 82px;
      padding: 14px 6px 14px 11px;
    }

    &__open {
      width: 34px;
      height: 34px;
      background-size: 19px 19px;
    }

    &__hidden {
      display: none;
      padding: 3px 12px 21px;
    }
  }
}
