.production {
  padding-top: 0;
  padding-bottom: 67px;

  &__cols {
    display: flex;
    align-items: flex-start;
  }

  &__nav {
    flex-shrink: 0;
    width: 333px;
    margin-right: 36px;
  }

  &__menu {
    background-color: #F7F7F7;
    border-radius: 15px;
    overflow: hidden;

    & > li {
      & > a {
        display: flex;
        align-items: center;
        height: 84px;
        padding: 0 10px 0 24px;
        font-weight: 500;
        font-size: 17px;
        line-height: 1.4;
        transition: background-color 0.3s;

        &:hover {
          background-color: #eceaea;
        }
      }

      &:not(:last-child) {
        & > a {
          border-bottom: 1px solid #D9D9D9;
        }
      }

      &.current {
        & > a {
          text-decoration: underline;
        }
      }
    }
  }

  &__tabs {
    width: calc(100% - 333px - 36px);
    margin-top: -5px;
  }

  &__title {
    display: flex;
    justify-content: center;
    margin: 0 0 40px;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;

    span {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -4px;
        width: 100%;
        height: 1px;
        background-color: $accent;
      }
    }
  }

  &__imgLink {
    display: block;
    transition: 0.3s;
    height: 262px;
    border-radius: 15px;
    overflow: hidden;
  }

  &__img {
    width: 100%;
    height: 100%;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__desc {
    margin: 12px 0 0;
    font-weight: 500;
    font-size: 20px;
  }

  &__wrap {
    position: relative;
    padding-bottom: 34px;
  }

  &__slider {
    overflow: hidden;;
  }

  &__btns {
    .swiper-button-prev,
    .swiper-button-next {
      top: 111px;
      width: 40px;
      height: 40px;
      transform: translateY(0);
    }

    .swiper-button-prev {
      left: -20px;
    }

    .swiper-button-next {
      right: -20px;
    }
  }

  &__address {
    padding: 31px 0 35px 38px;
    border-top: 1px solid #D9D9D9;
    border-bottom: 1px solid #D9D9D9;
    background-image: url("../img/pin.svg");
    background-repeat: no-repeat;
    background-size: 24px 24px;
    background-position: left center;
    font-weight: 500;
    font-size: 20px;
  }

  &__content {
    padding-top: 32px;

    .article {
      font-size: 14px;
      line-height: 1.65;
      h2 {
        margin-top: 0;
        font-size: 40px;
      }

      h3 {
        margin-top: 0;
        font-size: 36px;
      }

      h4 {
        margin-top: 0;
        font-size: 30px;
      }

      p {
        margin-bottom: 15px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &__menuOpen {
    display: none;
    margin-bottom: 5px;
  }

  @include max(1650) {
    &__nav {
      width: 283px;
      margin-right: 26px;
    }

    &__menu {
      & > li {
        & > a {
          height: 65px;
          padding: 0 14px;
          font-size: 16px;
        }
      }
    }

    &__tabs {
      width: calc(100% - 309px);
    }

    &__imgLink {
      height: 212px;
    }

    &__desc {
      margin: 8px 0 0;
      font-size: 17px;
    }

    &__address {
      padding: 17px 0 17px 38px;
      background-size: 20px 20px;
      font-size: 17px;
    }

    &__btns {
      .swiper-button-prev,
      .swiper-button-next {
        top: 91px;
      }
    }
  }

  @include max(1200) {
    &__content {
      padding-top: 22px;

      .article {
        h2 {
          font-size: 30px;
        }

        h3 {
          font-size: 26px;
        }

        h4 {
          font-size: 20px;
        }
      }
    }
  }

  @include max(1024) {
    &__title {
      margin-bottom: 30px;
      font-size: 14px;
    }
  }

  @include max(768) {
    padding-bottom: 37px;

    &__cols {
      display: block;
    }

    &__nav {
      position: relative;
      width: 100%;
    }

    &__menuOpen {
      display: block;
    }

    &__menu {
      position: absolute;
      top: calc(100% - 6px);
      left: 0;
      display: none;
      width: 100%;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      z-index: 50;

      & > li {
        & > a {
          height: 48px;
          font-size: 15px;
        }
      }
    }

    &__tabs {
      width: 100%;
      padding-top: 20px;
    }

    &__title {
      margin-bottom: 20px;
      font-size: 12px;

      span {
        &::after {
          bottom: -1px;
        }
      }
    }
  }

  @include max(576) {
    &__slider {
      overflow: visible;
    }

    &__btns {
      display: none;
    }

    &__imgLink {
      height: 152px;
    }

    &__desc {
      font-size: 15px;
      line-height: 1.3;
    }

    &__address {
      padding: 12px 0 12px 22px;
      background-size: 16px 16px;
      font-size: 14px;
    }
  }

  @include max(470) {
    &__desc {
      font-size: 14px;
    }
  }
}
