.about {
  padding-top: 71px;
  padding-bottom: 73px;

  &_page {
    padding-top: 0;
    padding-bottom: 0;
  }

  &__wrap {
    position: relative;
    padding: 65px 209px 65px 52px;
    background: transparent url("../img/about-bg.jpg") no-repeat center center;
    background-size: cover;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    padding: 66px 123px 97px 49px;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(20px);
    border-radius: 10px;

    &_oneCol {
      display: block;
      max-width: 843px;
      padding: 18px 70px 74px 48px;
    }
  }

  &__nums {
    flex-shrink: 0;
    max-width: 200px;
    margin-right: 20px;

    &_twoCols {
      display: flex;
      justify-content: space-between;
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 22px;
    }
  }

  &__num {
    position: relative;
    margin-left: -5px;
    padding-left: 40px;
    font-weight: 700;
    font-size: 40px;
    background: transparent url("../img/flag.svg") no-repeat left center;
    background-size: 24px 24px;

    span {
      display: inline-block;
      margin-top: -2px;
    }
  }

  &__numsItem {
    &:not(:last-child) {
      margin-bottom: 40px;
    }

    &_oneCol {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }

  &__numsTxt {
    font-weight: 500;
    line-height: 1.65;

    &_big {
      font-size: 18px;
    }
  }

  &__txt {
    max-width: 723px;
    font-weight: 500;
    line-height: 1.65;

    &_big {
      max-width: 100%;
    }

    p {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__director {
    position: absolute;
    right: 16px;
    bottom: 0;
    display: flex;
    align-items: flex-end;

    &_mobile {
      display: none;
    }

    &_page {
      right: 306px;
    }
  }

  &__directorInfo {
    margin-bottom: 31px;
    margin-right: -32px;
    padding: 8px 19px 12px;
    background-color: #FFFFFF;
    border-radius: 10px;
    font-weight: 500;
    line-height: 1.5;
  }

  &__directorName {
    font-size: 24px;
  }

  &__directorImg {
    flex-shrink: 0;
    width: 331px;
  }

  &__video {
    margin-top: 83px;
    padding-top: 52px;
    padding-bottom: 86px;
  }

  &__videoWrap {
    max-width: 1194px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 49px;
    border-radius: 15px;
    overflow: hidden;

    iframe {
      height: 548px;
    }
  }

  @include max(1650) {
    &__content {
      padding: 46px 50px 67px;
    }

    &__director {
      &_page {
        right: 56px;
      }
    }

    &__directorName {
      font-size: 18px;
    }

    &__directorImg {
      width: 251px;
    }

    &__directorOccupation {
      font-size: 13px;
    }

    &__num {
      margin-left: -5px;
      padding-left: 23px;
      font-size: 36px;
      background-size: 15px 15px;
    }

    &__numsItem {
      margin-bottom: 28px;

      &:not(:last-child) {
        margin-bottom: 28px;
      }
    }

    &__numsTxt {
      font-size: 14px;
    }

    &__txt {
      font-size: 14px;
    }

    &__video {
      margin-top: 53px;
      padding-top: 32px;
      padding-bottom: 46px;
    }

    &__videoWrap {
      margin-top: 29px;
    }
  }

  @include max(1200) {
    &__videoWrap {
      iframe {
        height: 468px;
      }
    }

    &__content {
      &_oneCol {
        padding-right: 70px;
      }
    }
  }

  @include max(1024) {
    &_page {
      padding-top: 10px;
      background: #FFFFFF url(../img/bg-wallpaper.jpg) repeat center center;
    }

    &__content {
      display: block;
    }

    &__nums {
      &_twoCols {
        display: block;
        max-width: 200px;
      }
    }

    &__numsTxt {
      &_big {
        font-size: 14px;
      }
    }

    &__video {
      margin-top: 13px;
    }
  }

  @include max(768) {
    &__wrap {
      padding: 0;
      background: none;
      box-shadow: none;
    }

    &__director {
      position: relative;
      right: auto;
      bottom: auto;

      &_mobile {
        display: block;
      }

      &_desktop {
        display: none;
      }
    }

    &__content {
      padding: 0 30px;
      background-color: transparent;
      backdrop-filter: none;
    }

    &__director {
      margin-bottom: 27px;
      margin-top: 38px;
    }

    &__directorInfo {
      margin-bottom: 0;
      margin-right: 0;
      padding: 5px 20px 8px 12px;
    }

    &__directorImg {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 210px;
    }

    &__directorOccupation {
      max-width: 145px;
    }

    &__videoWrap {
      iframe {
        height: 340px;
      }
    }
  }

  @include max(576) {
    padding-top: 38px;
    padding-bottom: 31px;

    &_page {
      padding-top: 10px;
      padding-bottom: 0;
    }

    &__content {
      padding: 0;
    }

    &__directorImg {
      right: -22px;
    }

    &__video {
      padding-bottom: 36px;
    }

    &__videoWrap {
      iframe {
        height: 250px;
      }
    }
  }

  @include max(374.98) {
    &__directorInfo {
      padding: 5px 10px;
    }

    &__directorImg {
      width: 190px;
    }

    &__directorOccupation {
      max-width: 135px;
    }

    &__directorName {
      font-size: 16px;
    }

    &__directorOccupation {
      font-size: 12px;
    }
  }
}
