.contacts {
  padding-top: 36px;
  padding-bottom: 43px;

  &__cols {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 36px;
  }

  &__col {
    display: flex;
    flex-direction: column;
    margin-bottom: 43px;
  }

  &__wrap {
    flex-grow: 1;
    background-color: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;
  }

  &__content {
    position: relative;
    padding: 25px 20px 38px 77px;
    background-image: url("../img/pin.svg");
    background-repeat: no-repeat;
    background-position: 39px 30px;
    background-size: 24px 24px;
  }

  &__inner {
    columns: 2;
    column-gap: 40px;
  }

  &__item {
    font-size: 20px;

    &:not(:last-child) {
      margin-bottom: 25px;
    }

    a {
      transition: color 0.3s;

      &:hover {
        color: $accent;
      }
    }

    b {
      font-weight: 500;
    }
  }

  &__map {
    height: 392px;
  }

  @include max(1650) {
    padding-top: 26px;
    padding-bottom: 33px;

    &__content {
      padding-left: 47px;
      background-position: 15px 30px;
    }

    &__item {
      font-size: 16px;

      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }

    &__map {
      height: 300px;
    }
  }

  @include max(992) {
    &__cols {
      display: block;
    }
  }

  @include max(576) {
    &__inner {
      columns: 1;
    }

    &__map {
      height: 250px;
    }
  }

  @include max(400) {
    &__content {
      padding-left: 28px;
      background-position: 6px 29px;
      background-size: 19px 19px;
    }

    &__col {
      margin-bottom: 23px;
    }

    &__item {
      font-size: 14px;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
