.footer {
  padding-top: 43px;
  padding-bottom: 49px;
  background-color: #2E271F;
  color: #FFFFFF;

  &__wrap {
    display: flex;
    justify-content: space-between;
  }

  &__col {
    width: 33.3%;

    &:not(:last-child) {
      padding-right: 20px;
    }
  }

  &__title {
    margin: 0 0 20px;
    font-weight: 700;
    font-size: 16px;
    color: $accent;
  }

  &__menu {
    & > li {
      &:not(:first-child) {
        margin-top: -4px;
      }

      & > a {
        display: inline-block;
        font-size: 14px;
        color: #FFFFFF;
        transition: color 0.3s;

        &:hover {
          color: $accent;
        }
      }
    }
  }

  &__text {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.57;

    b {
      font-weight: 500;
    }

    a {
      color: #FFFFFF;
      transition: color 0.3s;

      &:hover {
        color: $accent
      }
    }
  }

  &__links {
    display: flex;
    margin-bottom: 20px;
  }

  &__link {
    font-weight: 700;
    color: #FFFFFF;
    transition: color 0.3s;

    &:hover {
      color: $accent
    }
  }

  &__msg {
    width: 19px;
    height: 20px;
    margin-left: 16px;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: transform 0.3s;

    &:hover {
      transform: translateY(-5px);
    }

    &--wa {
      background-image: url("../img/icon-wa.svg");
    }

    &--viber {
      background-image: url("../img/icon-viber.svg");
    }
  }

  @include max(768) {
    padding-top: 26px;
    padding-bottom: 36px;

    &__wrap {
      display: block;
    }

    &__col {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 10px;
        padding-right: 0;
      }
    }

    &__title {
      margin-bottom: 0;
    }
  }
}
