.article {
  p {
    margin-bottom: 10px;
  }

  a {
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      text-decoration: underline;
    }
  }

  img {
    width: auto;
    max-width: 100%;
    height: auto;
    display: block;

    &.alignleft {
      max-width: 50%;
      float: left;
      margin: 0 20px 20px 0;
    }

    &.alignright {
      max-width: 50%;
      float: right;
      margin: 0 0 20px 20px;
    }

    &.aligncenter {
      margin: 0 auto;
    }
  }

  ul, ol {
    padding-bottom: 1.5em;
    padding-left: 0;
    padding-right: 1.5em;
  }

  ul li,
  ol li {
    transform: translateX(1.5em);
  }

  h2, h3, h4 {
    line-height: 1.2;
    font-weight: 500;
  }

  h2 {
    margin-top: 35px;
    margin-bottom: 20px;
    font-size: 18px;
  }

  h3 {
    margin-top: 25px;
    margin-bottom: 15px;
    font-size: 16px;
  }

  h4 {
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 14px;
  }
}
