.certs {
  &_about {
    padding-top: 86px;
    padding-bottom: 112px;
  }

  &__link {
    display: block;
    background-color: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    transition: box-shadow 0.3s;
    overflow: hidden;

    &:hover {
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
    }
  }

  &__cols {
    display: flex;
  }

  &__img {
    display: block;
  }

  &__sliderWrap {
    position: relative;
  }

  &__slider {
    margin: -20px;
    padding: 20px;
    overflow: hidden;
  }

  @include max(1200) {
    &_about {
      padding-top: 36px;
      padding-bottom: 42px;
    }

    &__btns {
      .swiper-button-prev,
      .swiper-button-next {
        width: 40px;
        height: 40px;
      }

      .swiper-button-prev {
        left: -17px;
      }

      .swiper-button-next {
        right: -17px;
      }
    }
  }

  @include max(992) {
    &__btns {
      display: none;
    }

    &__slider {
      overflow: visible;
    }
  }
}
