.catalog {
  &_card {
    padding-top: 37px;
  }

  &__cols {
    display: flex;
    align-items: flex-start;
  }

  &__nav {
    flex-shrink: 0;
    width: 333px;
    margin-right: 36px;
  }

  &__main {
    .section-title {
      text-align: left;
    }
  }

  &__menuOpen {
    display: none;
  }

  &__menuInner {
    padding: 42px 0 20px;
    background: #F7F7F7;
    border-radius: 15px;

    &:not(:last-child) {
      margin-bottom: 29px;
    }
  }

  &__menu {
    & > li {
      & > a {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 19px 17px 19px 24px;
        font-weight: 500;
        font-size: 17px;
        line-height: 1.4;
        transition: background-color 0.3s;

        &:hover {
          background-color: #eceaea;
        }
      }

      &:not(:last-child) {
        & > a {
          border-bottom: 1px solid #D9D9D9;
        }
      }

      &.current {
        .catalog__menuText {
          text-decoration: underline;
        }

        .catalog__menuSum {
          border-color: $accent;
        }
      }
    }
  }

  &__menuText {
    margin-top: 2px;
  }

  &__menuSum {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 41px;
    height: 31px;
    margin-left: 30px;
    background: #FFFFFF;
    border-radius: 6px;
    font-weight: 500;
    font-size: 14px;
    color: #686868;
    border: 1px solid transparent;
  }

  &__menuTitle {
    margin-bottom: 17px;
    font-weight: 700;
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;
  }

  &__menuBtns {
    display: flex;
    justify-content: center;
    padding-top: 24px;

    .xbtn {
      min-width: 148px;
      height: 50px;
    }
  }

  &__info {
    margin-bottom: 24px;
    padding: 26px 288px 39px 53px;
    background-color: #F7F7F7;
    background-image: url("../img/little-hause.png"), url("../img/flag.svg");
    background-position: right center, 17px 28px;
    background-size: 227px 170px, 24px 24px;
    background-repeat: no-repeat;
    border-radius: 15px;
  }

  &__infoTitle {
    margin-bottom: 9px;
    font-weight: 700;
    line-height: 1.6;
    text-transform: uppercase;
  }

  &__infoDesc {
    line-height: 1.4;

    b {
      font-weight: 500;
    }
  }

  &__sort {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 27px;
  }

  &__tabsNav {
    justify-content: flex-start;

    & > li {
      &:not(:last-child) {
        margin-right: 14px;
      }
    }
  }

  &__tabsBtn {
    &::after {
      bottom: -4px;
      box-shadow: 0px 7px 10px rgba(203, 65, 62, 0.3);
    }

    .xbtn {
      min-width: 237px;
      font-size: 14px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 27px 37px;
  }

  &__item {
    background: #F7F7F7;
    border-radius: 15px;
  }

  &__link {
    display: block;
    height: 11.5vw;
    border-radius: 15px;
    overflow: hidden;

    &:hover {
      .catalog__img {
        transform: scale(1.1);
      }
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
  }

  &__titleLink {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px;
    border-bottom: 1px solid $accent;

    &:hover {
      .catalog__title {
        color: $accent;
      }
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.6;
    text-transform: uppercase;
    transition: color 0.3s;
  }

  &__square {
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.6;
  }

  &__content {
    padding: 13px 10px 27px;
  }

  &__add {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px 12px;
    border-bottom: 1px solid #D9D9D9;
  }

  &__price {
    font-weight: 500;
    text-transform: uppercase;
  }

  &__priceDesc {
    display: block;
    margin-bottom: -2px;
    font-weight: 400;
    font-size: 14px;
    color: #686868;
    text-transform: none;
  }

  &__priceText {
    position: relative;
  }

  &__priceTooltip {
    position: absolute;
    top: 50%;
    right: -21px;
    transform: translateY(-50%);
  }

  &__more {
    display: block;
    font-size: 14px;
    color: $accent;
    border-bottom: 1px dotted $accent;

    &:hover {
      border-color: transparent;
    }
  }

  &__text {
    margin-bottom: 11px;
    font-size: 14px;
    text-align: center;
  }

  &__btns {
    & > li {
      &:not(:last-child) {
        margin-right: 8px;
      }

      input[type="radio"] {
        display: none;

        &:checked ~ span {
          &::before {
            box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.35);
            background-image: none;
            background-color: #FE864A;
          }

          &::after {
            opacity: 0;
          }
        }
      }

      .xbtn {
        padding: 9px 10px;
        min-width: 120px;
        font-size: 14px;
      }
    }
  }

  &__btn {
    &::after {
      bottom: -3px;
      box-shadow: 0px 7px 10px rgba(203, 65, 62, 0.3);
    }
  }

  &__btnsMore {
    display: none;
  }

  &__sliderWrap {
    position: relative;
  }

  &__slider {
    overflow: hidden;
  }

  &__sliderBtns {
    .swiper-button-prev {
      left: -61px;
    }

    .swiper-button-next {
      right: -61px;
    }
  }

  @include max(1650) {
    &__nav {
      width: 283px;
      margin-right: 10px;
    }

    &__menuInner {
      padding-top: 20px;
    }

    &__menu {
      & > li {
        & > a {
          padding: 15px 12px 15px 15px;
          font-size: 14px;
        }
      }
    }

    &__info {
      margin-bottom: 24px;
      padding: 23px 218px 23px 43px;
      background-position: right center, 17px 28px;
      background-size: 177px 130px, 16px 16px;
      font-size: 14px;
    }

    &__infoDesc {
      font-size: 13px;
    }

    &__list {
      grid-gap: 17px;
    }

    &__title {
      font-size: 14px;
    }

    &__square {
      font-size: 14px;
    }

    &__priceDesc {
      font-size: 13px;
    }

    &__priceText {
      font-size: 14px;
    }

    &__more {
      font-size: 13px;
    }

    &__text {
      font-weight: 500;
      font-size: 12px;
    }

    &__btns {
      & > li {
        .xbtn {
          padding: 6px 6px;
          min-width: 100px;
          font-size: 12px;
        }
      }
    }

    &__content {
      padding-bottom: 17px;
    }
  }

  @include max(1300) {
    &__sliderBtns {
      .swiper-button-prev,
      .swiper-button-next {
        width: 40px;
        height: 40px;
      }

      .swiper-button-prev {
        left: -25px;
      }

      .swiper-button-next {
        right: -25px;
      }
    }
  }

  @include max(1200) {
    &__sort {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 17px;
    }

    &__sortItem {
      margin-top: 15px;
    }

    &__sliderBtns {
      display: none;
    }

    &__slider {
      overflow: visible;
    }
  }

  @include max(1070) {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__link {
      height: 18.5vw;
    }

    &__tabsNav {
      & > li {
        &:not(:last-child) {
          margin-right: 8px;
        }
      }
    }

    &__tabsBtn {
      .xbtn {
        min-width: 170px;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 11px;
        line-height: 1.2;
      }
    }
  }

  @include max(992) {
    &__info {
      display: none;
    }

    &__list {
      grid-gap: 10px 8px;
    }

    &__btns {
      & > li {
        .xbtn {
          min-width: 0;
          padding-left: 10px;
          padding-right: 10px;
          font-weight: 500;
          font-size: 11px;
        }
      }
    }

    &__text {
      margin-bottom: 7px;
      text-align: left;
      line-height: 1.2;
    }

    &__add {
      padding: 7px 8px 10px;
    }

    &__more {
      display: none;
    }

    &__priceDesc {
      display: inline;
    }

    &__titleLink {
      margin-left: 8px;
      margin-right: 8px;
      padding: 5px 0 7px;
    }

    &__itemMain {
      position: relative;
      margin-top: -5px;
      background-color: #F7F7F7;
      border-radius: 6px;
      z-index: 10;
    }

    &__link {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  @include max(768) {
    &_card {
      padding-top: 30px;
    }

    &__nav {
      position: relative;
      width: 100%;
      margin-right: 0;
      margin-bottom: 0;
    }

    &__menuOpen {
      display: flex;
    }

    &__menuInner {
      position: absolute;
      top: calc(100% - 6px);
      left: 0;
      display: none;
      width: 100%;
      padding-top: 5px;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      z-index: 50;
    }

    &__menuTitle {
      display: none;
    }

    &__list {
      grid-template-columns: repeat(3, 1fr);
    }

    &__cols {
      display: block;
    }

    &__tabsNav {
      justify-content: center;
      margin-bottom: 25px;
    }

    &__sort {
      margin-bottom: 5px;
    }

    &__sortItem {
      margin-top: 5px;
    }

    &__btnsMore {
      display: flex;
      justify-content: center;
      padding-top: 22px;
    }
  }

  @include max(576) {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__link {
      height: 26.5vw;
    }

    &__content {
      padding: 7px 4px 12px;
    }

    &__priceDesc {
      font-size: 12px;
    }

    &__priceText {
      font-size: 12px;
    }

    &__title {
      font-size: 13px;
    }

    &__square {
      font-size: 12px;
    }
  }

  @include max(360) {
    &__list {
      grid-template-columns: repeat(1, 1fr);
    }

    &__link {
      height: 51.5vw;
    }

    &__tabsNav {
      flex-direction: column;

      & > li {
        width: 100%;
        margin-right: 0;

        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
    }

    &__tabsBtn {
      width: 100%;
    }
  }
}
