.reviews {
  padding-top: 34px;
  padding-bottom: 87px;

  &__sectionDesc {
    margin-bottom: 30px;
  }

  &__btns {
    margin-bottom: 43px;
    display: flex;
    justify-content: center;
  }

  &__btn {
    padding-left: 60px;
    padding-right: 60px;
    margin-left: auto;
    margin-right: auto;
  }

  &__wrap {
    position: relative;
  }

  &__photosWrap {
    position: relative;
    width: calc(50% - 18px);
  }

  &__photosInner {
    position: relative;
  }

  &__photosBtns {
    .swiper-button-prev,
    .swiper-button-next {
      width: 45px;
      height: 45px;
    }

    .swiper-button-prev {
      left: -17px;
    }

    .swiper-button-next {
      right: -17px;
    }
  }

  &__cols {
    display: flex;
    justify-content: space-between;
    padding-top: 43px;
  }

  &__videos {
    padding: 20px;
    margin: -20px;
    overflow: hidden;
  }

  &__video {
    position: relative;
    display: inline-block;
    width: 100%;
    border-radius: 25px;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 101px;
      background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 94.44%);
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;

    &_hidden {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 77px;
      height: 77px;
      border-radius: 50%;
      background-color: #FE864A;
      background-image: url("../img/play.svg");
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 23px 30px;
      box-sizing: border-box;
    }
  }

  &__videoInner {
    position: relative;
    width: 100%;

    iframe {
      display: block;
      width: 100%;
      height: 380px;
    }
  }

  &__videoTitle {
    position: absolute;
    bottom: 23px;
    left: 40px;
    right: 40px;
    margin: 0;
    font-weight: 500;
    font-size: 24px;
    color: #FFFFFF;
    z-index: 2;
  }

  &__photos {
    padding: 20px;
    margin: -20px;
    overflow: hidden;
  }

  &__photoLink {
    display: block;
    background-color: #FFFFFF;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.2);
    }

    &_mobile {
      display: none;
    }
  }

  &__photosTitle {
    margin: 0 0 29px;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.4;
  }

  @include max(1650) {
    &__videoInner {
      iframe {
        height: 310px;
      }
    }

    &__photosTitle {
      margin: 0 0 15px;
      font-size: 26px;
    }

    &__cols {
      padding-top: 33px;
    }

    &__overlay {
      &::before {
        width: 71px;
        height: 71px;
        border-radius: 50%;
        background-size: 17px 21px;
      }
    }

  }

  @include max(992) {
    &__videoTitle {
      left: 20px;
      right: 20px;
      font-size: 17px;
    }

    &__videoInner {
      iframe {
        height: 223px;
      }
    }
  }

  @include max(768) {
    padding-bottom: 42px;

    &__photosTitle {
      margin-bottom: 15px;
      font-size: 20px;
    }

    &__cols {
      display: block;
    }

    &__videoTitle {
      left: 10px;
      right: 10px;
      font-size: 16px;
    }

    &__photosWrap {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    &__videosBtns,
    &__photosBtns {
      display: none;
    }

    &__btns {
      margin-bottom: 30px;
    }

    &__btn {
      padding-left: 27px;
      padding-right: 27px;
    }
  }

  @include max(576) {
    &__photoLink {
      &_mobile {
        display: block;
      }

      &_desktop {
        display: none;
      }
    }

    &__video {
      &::after {
        content: none;
      }
    }

    &__videoTitle {
      position: static;
      font-size: 14px;
      color: #000000;
    }

    &__videoInner {
      margin-bottom: 11px;
      border-radius: 25px;
      overflow: hidden;
    }

    &__video {
      overflow: visible;
    }

    &__photoLink {
      box-shadow: none;
    }
  }
}
