@mixin placeholder($color, $size) {
  &::-webkit-input-placeholder {
    color: $color;
    font-size: $size;
    transition: 0.5s;
    font-family: $form-font;
  }

  &::-moz-placeholder {
    color: $color;
    font-size: $size;
    transition: 0.5s;
    font-family: $form-font;
  }

  &:-ms-input-placeholder {
    color: $color;
    font-size: $size;
    transition: 0.5s;
    font-family: $form-font;
  }
}
