$orange: #FE864A;
$grey: #F7F7F7;

$accent: $orange;

$icon-color: $orange;
$text-color: #000000;

$grad: linear-gradient(0deg, #FF634D 0%, #FE864A 61.54%, #FE864A 100%);

$font: "TTNorms", "Arial", "Helvetica Neue", sans-serif;

$form-font: $font;
$title-font: $font;

$content-width: 1440px;
$content-width-small: 1170px;
$container-offset: 16px;
$container-width: calc($content-width + $container-offset * 2);
