.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: box-shadow 0.4s ease-in-out, background-color 0.4s ease-in-out;
  z-index: 300;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 12px;
    padding-bottom: 16px;
  }

  &__logo {
    margin-right: 20px;
  }

  &__links {
    margin-left: 20px;
    text-align: right;
  }

  &__phones {
    display: flex;
    align-items: center;
  }

  &__info {
    display: flex;
    align-items: center;
  }

  &__text {
    font-size: 14px;

    &:not(:last-child) {
      margin-right: 61px;
    }

    b {
      display: block;
      font-weight: 700;
    }

    a {
      text-decoration: none;
      outline: none;
      transition: color 0.3s;

      &:hover {
        color: $accent;
      }
    }
  }

  &__msg {
    display: block;
    text-decoration: none;
    outline: none;
    width: 19px;
    height: 20px;
    margin-right: 11px;
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: transform 0.3s;

    &:hover {
      transform: translateY(-5px);
    }

    &--wa {
      background-image: url("../img/icon-wa.svg");
    }

    &--viber {
      background-image: url("../img/icon-viber.svg");
    }
  }

  &__link {
    display: block;
    text-decoration: none;
    outline: none;
    font-weight: 700;
    transition: color 0.3s;

    &:hover {
      color: $accent;
    }
  }

  &__modalLink {
    position: relative;
    font-size: 14px;
    transition: color 0.3s;

    &:hover {
      color: $accent;

      &::after {
        border-color: $accent;
      }
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -6px;
      left: 0;
      width: 100%;
      border-bottom: 1px dotted #000000;
      transition: border-color 0.3s;
    }
  }

  &__bottom {
    background-color: $accent;
    border-radius: 8px;
  }

  &__nav {
    display: flex;
    align-items: center;
    padding: 9px 9px 9px 43px;
  }

  &__menu {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & > li {
      &:not(:last-child) {
        margin-right: 10px;
      }

      & > a {
        position: relative;
        display: block;
        text-decoration: none;
        outline: none;
        font-weight: 500;
        font-size: 17px;
        text-transform: uppercase;
        color: #FFFFFF;

        &::after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          display: block;
          width: 100%;
          height: 2px;
          border-radius: 1.5px;
          background: #FFFFFF;
          transition: opacity 0.3s, transform 0.3s;
          transform: scale(0.4);
          opacity: 0;
          transform-origin: center center;
        }

        &:hover {
          &::after {
            opacity: 1;
            transform: scale(1);
          }
        }
      }
    }
  }

  &__burger {
    flex-shrink: 0;
    margin-left: 66px;
  }

  &__add {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    display: none;
  }

  &__addWrap {
    display: flex;
    background-color: #FFFFFF;
    border-radius: 0px 0px 10px 10px;
  }

  &__addCol {
    position: relative;
    width: 34.236%;
    padding: 30px 20px 30px 48px;

    &:nth-child(2) {
      width: 40.208%;
    }

    &:nth-child(3) {
      width: 25.556%;
    }

    &:not(:last-child) {
      &::after {
        position: absolute;
        top: 0;
        right: 0;
        content: "";
        width: 1px;
        height: 100%;
        background-color: #A9B5C0;
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      }
    }
  }

  &__addMenu {
    display: block;
    height: auto !important;

    &_mobile {
      display: none;
    }

    &:not(:last-child) {
      margin-bottom: 59px;
    }

    & > li {
      &:not(:last-child) {
        margin-bottom: 12px;
      }

      & > a {
        position: relative;
        font-size: 18px;
        transition: color 0.4s ease-in-out;

        &:not(.accent-color) {
          color: #686868;
        }

        &:hover {
          color: $accent;
        }
      }
    }
  }

  &__title {
    margin: 0 0 22px;
    font-weight: 500;
    font-size: 18px;
    text-transform: uppercase;
  }

  &.fixed {
    background-color: #FFFFFF;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);

    .header__top {
      padding-top: 6px;
      padding-bottom: 6px;
    }

    .header__add {
      border-top: 1px solid #A9B5C0;
    }

    .header__addWrap {
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    }

    .logo__img {
      width: 153px;
      height: 52px;
    }

    .logo__text {
      display: none;
    }

    .header__link {
      font-size: 13px;
    }

    .header__msg {
      width: 16px;
      height: 16px;
    }

    .header__burger {
      width: 30px;
      height: 30px;
    }
  }

  @include max(1650) {
    &__addCol {
      padding: 20px 20px 20px 38px;
    }

    &__title {
      margin-bottom: 12px;
      font-size: 16px;
    }

    &__addMenu {
      &:not(:last-child) {
        margin-bottom: 29px;
      }

      & > li {
        &:not(:last-child) {
          margin-bottom: 3px;
        }

        & > a {
          font-size: 16px;
        }
      }
    }

    &__info {
      display: none;
    }

    &__links {
      margin-left: auto;
    }
  }

  @include max(1220) {
    &__addCol {
      padding: 15px;
    }

    &__title {
      margin-bottom: 8px;
      font-size: 15px;
    }

    &__addMenu {
      &:not(:last-child) {
        margin-bottom: 20px;
      }

      & > li {
        &:not(:last-child) {
          margin-bottom: 2px;
        }

        & > a {
          font-size: 14px;
        }
      }
    }

    &__nav {
      padding-left: 20px;
    }

    &__menu {
      & > li {
        & > a {
          font-size: 14px;
        }
      }
    }
  }

  @include max(992) {
    &__add {
      position: fixed;
      top: 0;
      left: 0;
      width: 400px;
      max-width: calc(100vw - 80px);

      .container {
        padding: 0;
      }
    }

    &__addWrap {
      flex-direction: column;
      height: 100vh;
      padding-top: 30px;
      overflow: auto;
    }

    &__addCol {
      width: 100% !important;

      &_regions {
        order: 1;
      }

      &:not(:last-child) {
        &::after {
          content: none;
        }
      }
    }

    &__addMenu {
      &_mobile {
        display: block;
      }
    }

    &__menu {
      display: none;
    }

    &__top {
      justify-content: flex-start;
    }

    &__links {
      margin-left: auto;
    }

    &__modalLink {
      display: none;
    }

    &__burger {
      margin-left: 26px;
    }

    &.fixed {
      .logo__img {
        width: 108px;
        height: 34px;
      }
    }
  }

  @include max(768) {
    &__add {
      width: 350px;
    }

    &__msg {
      display: none;
    }

    &__addCol {
      padding: 5px 15px;
    }

    &__title {
      margin-bottom: 4px;
      font-size: 14px;
    }

    &__addMenu {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }

    &__link {
      font-size: 15px;
    }
  }

  @include max(374.98) {
    &__burger {
      margin-left: 10px;
    }

    &__link {
      font-size: 13px;
    }

    &.fixed {
      .logo__img {
        width: 98px;
        height: 26px;
      }
    }
  }
}
