.media {
  padding-top: 26px;
  padding-bottom: 57px;

  &__link {
    display: block;

    &:hover .media__content {
      box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
    }
  }

  &__cols {
    display: grid;
    grid-template-columns: repeat(2, 2fr);
    grid-gap: 36px;
  }

  &__social {
    display: block;
    width: 56px;
    height: 56px;
    margin: 0 auto 16px;
    background-color: transparent;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;

    &_vk {
      background-image: url("../img/vk.svg");
    }

    &_yt {
      background-image: url("../img/yt.svg");
    }
  }

  &__content {
    display: block;
    margin-bottom: 22px;
    height: 410px;
    border-radius: 25px;
    overflow: hidden;
    transition: box-shadow 0.3s;

    iframe {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
  }

  @include max(1650) {
    &__content {
      height: 280px;
    }
  }

  @include max(768) {
    padding-top: 19px;
    padding-bottom: 39px;

    &__cols {
      display: block;
    }

    &__link {
      max-width: 450px;
      margin-left: auto;
      margin-right: auto;
    }

    &__social {
      width: 41px;
      height: 41px;
    }

    &__content {
      margin-bottom: 16px;
    }

    &__title {
      font-size: 18px;
      line-height: 1.4;
    }

    &__item {
      &:not(:last-child) {
        margin-bottom: 23px;
      }
    }
  }

  @include max(576) {
    &__content {
      height: 200px;
    }
  }
}
