.scr1 {
  position: relative;
  padding-top: 226px;
  padding-bottom: 55px;
  z-index: 1;

  &__inscr {
    margin-bottom: 10px;
  }

  &__content {
    max-width: 745px;
  }

  &__info {
    margin-bottom: 47px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.388; /* 50 / 36 */

    strong {
      font-weight: 700;
      font-size: 48px;
      text-transform: uppercase;
    }

    b {
      font-weight: 500;
      font-size: 40px;
    }

    p {
      margin-top: 10px;
    }
  }

  &__btns {
    margin-bottom: 79px;
  }

  &__features {
    max-width: 1071px;
  }

  &__img {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    max-width: none;
    z-index: -1;
  }

  &__house {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 930px;
    z-index: -1;
  }

  @include max(1650) {
    padding-top: 226px;
    padding-bottom: 55px;
    min-height: 100vh;

    &__features {
      display: none;
    }

    &__title {
      font-size: 26px;

      strong {
        font-size: 37px;
      }

      b {
        font-size: 30px;
      }
    }

    &__info {
      margin-bottom: 107px;
    }

    &__btns {
      margin-bottom: 0;
    }

    &__img {
      width: 110%;
    }

    &__house {
      width: 690px;
    }
  }

  @include max(1300) {
    &__content {
      max-width: 645px;
    }

    &__title {
      font-size: 22px;

      strong {
        font-size: 34px;
      }

      b {
        font-size: 26px;
      }
    }

    &__house {
      right: -80px;
      width: 640px;
    }
  }

  @include max(1200) {
    padding-top: 186px;
  }

  @include max(1024) {
    padding-bottom: 35px;
    min-height: 0;

    &__house {
      right: -80px;
      width: 620px;
    }

    &__btns {
      margin-bottom: 49px;
    }
  }

  @include max(992) {
    padding-top: 76px;

    &__content {
      max-width: 405px;
    }

    &__info {
      margin-bottom: 15px;
    }

    &__title {
      font-size: 18px;

      strong {
        font-size: 24px;
      }

      b {
        font-size: 20px;
      }
    }

    &__btns {
      margin-bottom: 0;
    }

    &__house {
      width: 430px;
    }
  }

  @include max(768) {
    &__content {
      max-width: 305px;
    }

    &__house {
      width: 340px;
    }
  }

  @include max(490) {
    padding-top: 75px;
    padding-bottom: 51px;

    &__title {
      width: calc(100% + 5px);
      margin-bottom: 300px;
      font-size: 20px;

      strong {
        font-size: 22px;
      }

      b {
        font-size: 22px;
      }
    }

    &__inscr {
      margin-bottom: 26px;
    }

    &__btns {
      text-align: center;
    }

    &__content {
      max-width: 100%;
    }

    &__house {
      display: none;
    }
  }
}
