.documents {
  padding-top: 30px;
  padding-bottom: 74px;

  &__sectionDesc {
    margin-bottom: 27px;
  }

  &__cols {
    display: flex;
    align-items: flex-start;
  }

  &__certLink {
    flex-shrink: 0;
    width: 579px;
    margin-right: 36px;
  }

  &__faq {
    flex-grow: 1;
    padding-top: 10px;
  }

  @include max(1650) {
    &__certLink {
      width: 439px;
      margin-right: 16px;
    }
  }

  @include max(992) {
    &__certLink {
      width: 319px;
    }
  }

  @include max(768) {
    padding-top: 25px;
    padding-bottom: 31px;

    &__cols {
      display: block;
    }

    &__certLink {
      max-width: 290px;
      margin: 0 auto 38px;
    }
  }
}
