.form {
  width: 100%;

  &__wrap {
    display: flex;
    align-items: flex-start;
    align-items: center;
    margin-bottom: 30px;

    .form__item {
      max-width: 287px;
      margin-right: 22px;
    }
  }

  input[type="submit"] {
    height: 70px;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input,
  textarea {
    font-family: $form-font;
  }

  .text-input {
    display: block;
    width: 100%;

    input,
    textarea {
      background: none;
      border: none;
      outline: none;
      transition: 0.3s;
      resize: none;

      width: 100%;
      height: 70px;
      padding-left: 34px;
      padding-right: 10px;
      background-color: #FFFFFF;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15);
      border-radius: 40px;
      @include placeholder(#686868, 14px);

      &:focus {
        @include placeholder(#FFFFFF, 14px);
      }

      &:hover {
        box-shadow: inset 0px 4px 4px rgba($accent, 0.5);
      }
    }

    textarea {
      height: 86px;
      padding: 13px 15px 13px 34px;
      border-radius: 20px;
      line-height: 1.65;
      color: #686868;
    }
  }

  &__title {
    margin-bottom: 26px;
    font-size: 24px;
    font-weight: 500;
    text-align: center;

    &_small {
      font-size: 20px;
    }
  }

  &_grey {
    padding: 24px 45px 48px;
    background-color: #F7F7F7;
    border-radius: 15px;

    .text-input {
      input {
        height: 50px;
      }
    }

    input[type=submit] {
      height: 65px;
    }

    .form__wrap {
      margin-bottom: 16px;

      .xbtn_form {
        margin-left: auto;
      }

      .form__item {
        max-width: 246px;
        margin-right: 13px;
      }
    }
  }

  .accept {
    display: block;

    &_centered {
      text-align: center;
    }

    label {
      display: block;
      width: 100%;
      font-weight: 400;
      font-size: 12px;
      color: #808080;
      cursor: pointer;
      transition: 0.3s;

      & > span {
        position: relative;
        display: inline-block;
        padding-left: 38px;
      }

      .accept__icon {
        position: absolute;
        top: 0;
        left: 3px;
        display: block;
        width: 19px;
        height: 19px;
        background-color: #FFFFFF;
        border: 2px solid $accent;
        transition: background-color 0.3s;

        &::before {
          content: "";
          position: absolute;
          top: calc(50% - 5px);
          left: calc(50% - 5px);
          display: block;
          width: 10px;
          height: 10px;
          background: transparent url("../img/tick.svg") no-repeat center center;
          background-size: contain;
          transition: 0.3s;
          opacity: 0;
        }
      }

      &:hover {
        color: $accent;
      }
    }

    input {
      display: none;
    }

    input:checked + span {
      .accept__icon {
        background-color: $accent;

        &::before {
          opacity: 1;
        }
      }
    }
  }

  &__cols {
    display: grid;
    grid-gap: 13px;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 22px;
  }

  &__btns {
    margin: 30px 0;
    text-align: center;
  }

  &__xbtn {
    min-width: 410px;
  }

  @include max(1650) {
    .text-input {
      input,
      textarea {
        padding-left: 15px;
      }
    }
  }

  @include max(1200) {
    &_grey {
      padding: 24px;

      input[type=submit] {
        min-width: 200px;
        height: 60px;
      }
    }
  }

  @include max(992) {
    input[type=submit] {
      height: 60px;
    }

    &_grey {
      text-align: center;

      .form__wrap {
        display: block;
        max-width: 287px;
        margin: 0 auto 40px;

        .form__item {
          max-width: 100%;
          margin-right: 0;
          margin-bottom: 15px;
        }
      }

      .xbtn_form {
        width: 100%;
      }

      input[type=submit] {
        min-width: 100%;
      }
    }

    &__title {
      font-size: 18px;
    }
  }

  @include max(768) {
    &__wrap {
      display: block;
      max-width: 287px;
      margin: 0 auto 40px;

      .form__item {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }

    input[type=submit] {
      width: 100%;
      height: 50px;
      padding-top: 10px;
      padding-bottom: 10px;
    }

    form {
      text-align: center;
    }

    .xbtn_form {
      width: 100%;
      margin-top: 9px;
    }

    &__title,
    &__desc {
      br {
        display: none;
      }
    }
  }

  @include max(450) {
    &__cols {
      display: block;
      margin-bottom: 0;

      .form__item {
        margin-bottom: 15px;
      }
    }
  }
}
