.lead {
  padding-top: 45px;
  padding-bottom: 67px;

  &_card {
    padding-top: 74px;

    .lead__wrap {
      padding: 42px 20px 47px 87px;
      background-image: url("../img/form-bg-2.jpg");
      background-position: center center;
    }

    .lead__title {
      margin-bottom: 20px;
      font-size: 36px;
      text-transform: uppercase;
    }

    .lead__desc {
      margin-bottom: 40px;
      font-size: 24px;
    }
  }

  &__wrap {
    padding: 85px 40px 85px 82px;
    background: #F7F7F7 url("../img/form-bg.jpg") no-repeat left center;
    background-size: cover;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
  }

  &__title {
    margin: 0 0 30px;
    max-width: 720px;
    font-weight: 700;
    font-size: 40px;
    text-transform: uppercase;
  }

  &__desc {
    max-width: 760px;
    margin-bottom: 62px;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.4;
  }

  &__formTitle {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 20px;
  }

  &__form {
    max-width: 588px;
    padding: 0;

    .text-input {
      input {
        height: 60px;
      }
    }
  }

  &__formBtns {
    margin-top: 24px;
    margin-bottom: 37px;

    input[type=submit] {
      height: auto;
      font-size: 20px;
      line-height: 1.4;
    }
  }

  @include max(1650) {
    &_card {
      .lead__wrap {
        padding: 32px 20px 27px 37px;
      }

      .lead__title {
        margin-bottom: 10px;
        font-size: 27px;
      }

      .lead__desc {
        margin-bottom: 20px;
        font-size: 18px;
      }
    }

    &__wrap {
      padding: 25px 40px 35px;
      background-position: top center;
    }

    &__title {
      max-width: 550px;
      font-size: 30px;
    }

    &__desc {
      max-width: 630px;
      margin-bottom: 22px;
      font-size: 26px;
    }

    &__formTitle {
      margin-bottom: 10px;
      font-size: 17px;
    }

    &__form {
      max-width: 508px;
    }
  }

  @include max(992) {
    &__wrap {
      padding: 25px;
    }

    &_card {
      .lead__wrap {
        background-image: none;
        text-align: center;
      }

      .lead__title {
        max-width: 100%;
      }

      .lead__desc {
        max-width: 100%;
      }
    }

    &__form {
      margin-left: auto;
      margin-right: auto;

      .xbtn_form {
        max-width: 300px;
      }
    }
  }

  @include max(768) {
    padding-top: 24px;
    padding-bottom: 27px;

    &__title {
      max-width: 500px;
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 1.4;
    }

    &__desc {
      max-width: 500px;
      margin-bottom: 16px;
      font-size: 16px;
    }

    &__formTitle {
      margin-bottom: 13px;
      font-weight: 400;
      font-size: 14px;
      line-height: 1.2;
      text-align: center;
    }
  }

  @include max(576) {
    &_card {
      .lead__wrap {
        padding: 25px 15px;
      }

      .lead__title {
        font-size: 22px;
      }

      .lead__desc {
        font-size: 16px;
      }
    }

    &__wrap {
      padding: 18px 13px 23px;
      background-image: url("../img/form-bg-mob.jpg");
    }

    &__title {
      width: calc(100% + 6px);

      br {
        display: none;
      }
    }

    &__desc {
      br {
        display: none;
      }
    }
  }

  @include max(450) {
    &_card {
      .lead__title {
        font-size: 19px;
      }

      .lead__desc {
        font-size: 14px;
      }
    }
  }
}
