.breadcrumbs {
  padding-top: 21px;
  padding-bottom: 25px;

  ol {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      position: relative;
      display: inline-block;

      &:not(:last-of-type) {
        &::after {
          content: ">";
          margin: 0 6px;
        }
      }

      a,
      span span {
        font-size: 14px;
        text-decoration: none;
        transition: color 0.3s;
      }

      a[href]:hover {
        color: $accent;
      }
    }

  }

  @include max(1650) {
    padding-top: 11px;
    padding-bottom: 15px;
  }

  @include max(576) {
    ol {
      li {
        a,
        span span {
          font-size: 13px;
        }
      }
    }
  }
}
