.seo {
  margin-top: -9px;

  &__wrap {
    padding-top: 34px;
    padding-bottom: 115px;
  }

  &__columns {
    padding-top: 58px;
    columns: 2;
    column-gap: 26px;
    font-size: 14px;
    line-height: 1.65;
    text-decoration: underline;
  }

  @include max(1200) {
    &__wrap {
      padding-bottom: 80px;
    }

    &__columns {
      padding-top: 30px;
    }
  }

  @include max(768) {
    margin-top: 0;
    padding-top: 10px;

    .section-title {
      text-align: left;
    }

    &__wrap {
      padding-top: 16px;
      padding-bottom: 40px;
    }

    &__columns {
      columns: 1;
      padding-top: 0;

      p {
        &:not(:last-child) {
          margin-bottom: 25px;
        }
      }
    }
  }
}
