.xbtn {
  position: relative;
  display: block;

  &_grad {
    min-width: 294px;
    padding: 22px 35px;
    background-color: rgba($accent, 0);
    border-radius: 50px;
    font-weight: 700;
    color: #FFFFFF;
    text-align: center;
    transition: background-color 0.3s;
    z-index: 2;

    &:hover {
      background-color: $accent;
    }
  }

  &_orange {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 215px;
    height: 57px;
    padding-left: 10px;
    padding-right: 10px;
    background-color: $accent;
    border: 2px solid $accent;
    border-radius: 40px;
    font-weight: 700;
    font-size: 14px;
    color: #FFFFFF;
    text-align: center;
    transition: background-color 0.3s, color 0.3s;

    &:hover {
      background-color: #FFFFFF;
      color: $accent;
    }
  }

  &_uppercase {
    text-transform: uppercase;
  }

  &__wrap {
    position: relative;
    display: inline-block;
    z-index: 1;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background-image: $grad;
      border-radius: 50px;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -5px;
      display: block;
      width: 100%;
      height: 100%;
      background-color: #CB413E;
      box-shadow: 0 15px 20px rgba(203, 65, 62, 0.3);
      border-radius: 50px;
      z-index: -1;
    }
  }

  @include max(992) {
    &_grad {
      min-width: 254px;
    }
  }

  @include max(768) {
    &_grad {
      &::after {
        box-shadow: 0px 8px 10px rgba(203, 65, 62, 0.3);
      }
    }

    &_orange {
      min-width: 188px;
      height: 38px;
      padding-left: 10px;
      padding-right: 10px;
      font-size: 13px;
    }
  }

  @include max(374.98) {
    &_grad {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}
