.card {
  padding-bottom: 113px;

  &__titleMain {
    text-align: left;
  }

  &__cols {
    display: flex;
  }

  &__main {
    width: calc(100% - 615px);
    margin-right: 36px;
  }

  &__aside {
    flex-shrink: 0;
    width: 579px;
  }

  &__btns {
    justify-content: flex-start;

    & > li {
      &:not(:last-child) {
        margin-right: 11px;
      }

      input[type="radio"] {
        display: none;

        &:checked ~ span {
          &::before {
            box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.12);
            background-image: none;
            background-color: #FE9E6E;
          }

          &::after {
            opacity: 0;
          }
        }
      }

      .xbtn {
        padding: 9px 15px;
        min-width: 188px;
        font-size: 14px;
      }
    }
  }

  &__btn {
    &::after {
      bottom: -3px;
      box-shadow: 0px 7px 10px rgba(203, 65, 62, 0.3);
    }
  }

  &__wrap {
    position: relative;
    margin-bottom: 31px;
  }

  &__btns {
    position: absolute;
    left: 19px;
    bottom: 21px;
  }

  &__slider {
    overflow: hidden;
  }

  &__imgLink {
    display: block;
  }

  &__img {
    border-radius: 15px;
    overflow: hidden;
  }

  &__thumbsWrap {
    position: relative;
    margin-bottom: 44px;
    padding-left: 19px;
    padding-right: 19px;
  }

  &__thumbs {
    padding: 20px;
    margin: -20px;
    overflow: hidden;
  }

  &__thumbsImg {
    height: 140px;
    border-radius: 15px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.7s;

    &:hover {
      transform: scale(1.05);
    }
  }

  &__thumbsBtns {
    .swiper-button-prev,
    .swiper-button-next {
      width: 40px;
      height: 40px;
    }

    .swiper-button-prev {
      left: -3px;
    }

    .swiper-button-next {
      right: -3px;
    }
  }

  &__nav {
    display: flex;

    & > li {
      &:last-child {
        & > a {
          border-right: none;
        }
      }

      &.current {
        & > a {
          &::before {
            opacity: 1;
          }

          span {
            &::after {
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &__navItem {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 14px 23px;
    background: linear-gradient(360deg, #A9B5C0 -24.24%, #EBEEF0 112.12%);
    border-right: 1px solid #D6E1EB;
    border-radius: 6px 6px 0 0;
    font-weight: 500;
    text-align: center;
    z-index: 1;

    &:hover {
      &::before {
        opacity: 1;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: linear-gradient(180deg, #DFE6ED -64.81%, rgba(223, 230, 237, 0) 100%), #FFFFFF;
      border-radius: 6px 6px 0 0;
      opacity: 0;
      transition: opacity 0.3s;
      z-index: -1;
    }

    span {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: $accent;
        opacity: 0;
        transition: opacity 0.3s;
      }
    }
  }

  &__tabs {
    padding: 39px 33px 40px;
    background-color: #FFFFFF;
    border: 1px solid #D6E0EB;
    box-shadow: 7px 10px 15px rgba(0, 0, 0, 0.1);
    border-radius: 0 0 15px 15px;
    line-height: 1.65;
  }

  &__tab {
    &:not(:first-child) {
      display: none;
    }
  }

  &__content {
    padding-right: 31px;
  }

  &__table {
    width: 100%;
    margin-top: 38px;
    border-collapse: collapse;

    thead {
      background-color: #D6E1EB;

      td,th {
        padding: 9px 10px 9px 28px;
        font-weight: 500;

        &:last-child {
          padding-right: 53px;
        }
      }
    }

    tbody {
      td {
        padding: 8px 15px 8px 26px;
        border-bottom: 1px solid #D6E0EB;
        font-weight: 500;
        text-transform: uppercase;

        &:last-child {
          padding-right: 35px;
        }

        b {
          font-weight: 500;
          font-size: 24px;
        }
      }
    }
  }

  &__download {
    margin-top: 74px;
    text-align: center;
  }

  &__schemeLink {
    display: flex;
    justify-content: center;
  }

  &__form {
    margin-bottom: 24px;
    padding-bottom: 21px;
  }

  &__video {
    margin-bottom: 21px;
    border-radius: 25px;
    overflow: hidden;
  }

  &__title {
    margin-bottom: 20px;
    text-align: center;
  }

  &__videoInner {
    iframe {
      height: 314px;
    }
  }

  @include max(1650) {
    padding-bottom: 23px;

    &__main {
      width: calc(100% - 494px);
      margin-right: 15px;
    }

    &__aside {
      width: 479px;
    }

    &__navItem {
      font-size: 14px;
    }

    &__tabs {
      padding: 25px 20px;
    }

    &__content {
      padding-right: 0;
      font-size: 14px;
    }

    &__table {
      margin-top: 28px;

      tbody {
        td {
          b {
            font-size: 20px;
          }
        }
      }
    }

    &__download {
      margin-top: 32px;
    }
  }

  @include max(1200) {
    &__navItem {
      padding: 14px;
    }

    &__main {
      width: calc(100% - 454px);
    }

    &__aside {
      width: 439px;
    }

    &__video {
      margin-bottom: 11px;
    }

    &__videoInner {
      iframe {
        height: 244px;
      }
    }
  }

  @include max(992) {
    &__cols {
      display: block;
    }

    &__main {
      width: 100%;
      margin-right: 0;
    }

    &__wrap {
      max-width: 550px;
      margin-left: auto;
      margin-right: auto;
    }

    &__aside {
      max-width: 100%;
      margin: 40px auto 0;
    }
  }

  @include max(768) {
    &__wrap {
      margin-bottom: 21px;
    }

    &__thumbsWrap {
      margin-bottom: 34px;
      padding: 0;
    }

    &__thumbsBtns {
      display: none;
    }

    &__thumbs {
      margin: 0;
      padding: 0;
      overflow: visible;
    }

    &__btns {
      left: 10px;
      bottom: 12px;

      & > li {
        .xbtn {
          min-width: 0;
          padding: 9px 15px;
          font-size: 11px;
        }
      }
    }

    &__thumbsImg {
      height: 110px;
    }

    &__navItem {
      padding: 10px;
      font-size: 13px;
    }
  }

  @include max(576) {
    &__nav {
      display: block;
    }

    &__tabs {
      width: calc(100% + 32px);
      margin-left: -16px;
    }

    &__table {
      width: 100%;

      thead {
        td,th {
          padding: 10px;

          &:last-child {
            padding-right: 10px;
            padding-left: 0;
          }
        }
      }

      tbody {
        td {
          padding: 10px;
          font-size: 10px;

          &:last-child {
            padding-right: 10px;
            padding-left: 0;
          }

          b {
            font-size: 13px;
          }
        }
      }
    }

    &__hide {
      display: none;
    }
  }

  @include max(450) {
    &__wrap {
      margin-bottom: 12px;
    }

    &__btns {
      left: 7px;
      bottom: 7px;

      & > li {
        &:not(:last-child) {
          margin-right: 5px;
        }

        .xbtn {
          padding: 5px 10px;
          font-size: 9px;
        }
      }
    }

    &__thumbsWrap {
      margin-bottom: 14px;
    }

    &__form {
      width: calc(100% + 32px);
      margin-left: -16px;
    }

    &__content {
      font-size: 13px;
    }
  }

  @include max(360) {
    &__form {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
